@font-face{
	font-family: 'icons';
	src: url('../fonts/sap-icofonts.woff');
}
.accordeon{
	width: 200px;
	margin-top: 20px;
	margin-left: 100px;
}
.input{
	display:none;
}
.item{
	border:2px solid green;
	margin:0;
	display: block;
	padding: 10px 25px;
	cursor: pointer;
	width: 150px;
	position: relative;
}
.description{
	border:1px solid green;
	width: 200px;
	max-height: 0;
	overflow: hidden;
	transition: max-height 0.2s ease;
}
.input:checked ~ .description{
	border:1px solid green;
	width: 200px;
	max-height: 300px;
	overflow: hidden;
	transition: max-height 0.5s ease;
}
.item:after{
	content: '\E00D';
	display: block;
	font-family: 'icons';
	position: absolute;
	top:50%;
	right: 20px;
	transform: translateY(-50%);
}
.input:checked ~ .item:after{
	content: '\E00F';
}